
<style lang="less" scoped>
.work-data-container{
  min-height: 100vh;
  background-color: #fff;
  padding-bottom: 100px;
}
.workDataItem{
  width: 95%;
  display: flex;
  justify-content: space-around;
  &>div{
    padding-top: 40px;
    &:nth-child(1){
      width: 12%;
      padding-left: 30px;
      &>img{
        width: 70px;
        height: 70px;
      }
    }
    &:nth-child(2){
      width: 65%;
      & > p:nth-child(1){
        & > span{
          font-size: 25px;
        }
      }
      &>p:nth-child(2){
        margin-top: 20px;
        &>span{
          &>img{
            height: 35px;
            width: 30px;
          }
        }
        &>.work-audio{
          &>img{
            width: 111px;
            height: 40px;
            vertical-align: middle;
            margin-right: 10px;
          }
          color: #3eb140;
        }
      }
    }
    &:nth-child(3){
      width: 23%;
      text-align: center;
      &>div{
        background-position: center center;
        overflow: hidden;
        height: 120px;
        width: 120px;
        background-size: cover;
        background-repeat: no-repeat;
        border-radius: 10px;
      }
    }
  }
}
.teacherClass{
  color: #3eb140;
}
.stuClass{
  color: #292929;
}
.datePoint{
  padding-top: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  &>p{
    &:nth-child(1){
      width: 15%;
    }
    &:nth-child(2){
      width: 75%;
      color: #969696;
      font-size: 24px;
      border-bottom: 1px solid #dcdcdc;
      padding-bottom: 20px;
    }
    &:nth-child(3){
      width: 10%;
    }
  }
}
/* 无列表的情况下，显示背景图片 */
.bg_img {
    width: 100%;
    text-align: center;
}
.bg_img>img{
    margin-top:30px;
    width: 330px;
    height: 316px;
}
.bg_img>p{
    margin-top: 20px;
    font-size: 30px;
    color: #464646;
}
</style>
<style>
  .emoji-convert{
    overflow : hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 2;
    -webkit-box-orient: vertical;
    padding: 0 20px;
    word-break: break-all;
  }
  .emoji-convert>img {
    width: 40px;
    height: 40px;
    vertical-align: middle;
  }
</style>

<template>
  <div class="work-data-container" :style="{'backgroundColor': WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST.length > 0 ? '#fff' : '#eee'}">
    <div v-if="WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST" v-for="(workItem, index) in WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST" :key="index">
        <div class="workDataItem">
        <div @click="__enterPerpleCenter(workItem)"><img :src="workItem.avatar" alt="头像"></div>
        <div @click.stop="__enterTaskDetail(workItem)">
          <p>
            <span v-if="workItem.infoType === 'RECOMMEND' || workItem.infoType === 'CORRECTION'" class="teacherClass">{{workItem.sendUserName}}</span>
            <span v-if="workItem.infoType !== 'RECOMMEND' && workItem.infoType !== 'CORRECTION'" class="stuClass">{{workItem.sendUserName}}</span>
            <span v-if="workItem.infoType === 'COMMENT'">评论了你的作业</span>
            <span v-if="workItem.infoType === 'PRAISE' && workItem.praiseNumber === 1"></span>
            <span v-if="workItem.infoType === 'PRAISE' && workItem.praiseNumber !== 1">等{{workItem.praiseNumber}}人赞了你</span>
            <span v-if="workItem.infoType === 'RECOMMEND'">推荐了你的作业</span>
            <span v-if="workItem.infoType === 'CORRECTION'">批改了你的作业</span>
            <span v-if="workItem.infoType === 'REPLY'">回复了你的评论</span>
          </p>
          <p>
            <span v-if="workItem.infoType === 'PRAISE' && workItem.praiseNumber === 1"><img src="../../assets/img/shouye_dainzan_select.png" alt="点赞"></span>
            <span class="work-audio" 
              v-if="workItem.infoType === 'CORRECTION' && workItem.approvedType && workItem.approvedType === 'AUDIO'">
              <audio :src="workItem.approvedAudio" ></audio>
              <img :src="workItem.playStatus ? playImg : pauseImg" alt="语音播放" @click.stop="changeAudioStatus(workItem, index)">{{workItem.approvedAudioLength}}"
            </span>
            <span class="emoji-convert" v-if="workItem.infoType === 'CORRECTION' && workItem.approvedType === 'TEXT'"  v-html="convertEmotions(workItem.approvedContent)"></span>
            <span class="emoji-convert" v-if="workItem.infoType === 'COMMENT' && workItem.commentType === 'TEXT'"  v-html="convertEmotions(workItem.comment)"></span>
            <span class="emoji-convert" v-if="workItem.infoType === 'REPLY' && workItem.commentType === 'TEXT'"  v-html="convertEmotions(workItem.comment)"></span>
          </p>
        </div>
        <div>
          <div  v-if="workItem.smallImg" @click="__enterTaskDetail(workItem)" :style="{'background-image': 'url('+ workItem.smallImg.split(',')[0] +')'}"></div>
        </div>
      </div>
      <div class="datePoint"  @click="__enterTaskDetail(workItem)">
        <p></p>
        <p>{{workItem.addTime | datePoint}}</p>
        <p></p>
      </div>
    </div>
    <section class="bg_img" v-if="WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST.length === 0">
      <img :src="noList" alt="没有作业啦">
      <p>还没有我的相关动态哦~</p>
    </section>
    <div style="padding: 10px 0 60px 0;text-align: center;background-color: #fff;color: #777;" v-if="falgNoData">没有更多了~</div>
  </div>
</template>
<script>
  import { createNamespacedHelpers } from 'vuex'
  const { mapMutations, mapActions, mapState} = createNamespacedHelpers('workRangeModule')
  export default {
    data() {
      return {
        show_bg: 0,
        falgNoData: false,
        pagination: true,
        noList: require('../../assets/img/kognbaiye_yuwoxiangguan.png'),
        playImg: require('../../assets/img/sound.gif'),
        pauseImg: require('../../assets/img/yuwoxiangguan_yuyin.png')
      }
    },
    computed: {
      ...mapState([
        'WORK_RANGE_ABOUT_OBJECT',
      ]),
      LOGIN_USER_OBJECT(){
        return this.$store.state.userModule.LOGIN_USER_OBJECT
      },
      workRangeSavePosition(){
        return this.$store.state.workRangeSavePosition.workRelatedMe
      }
    },
    created () {

      if(this.LOGIN_USER_OBJECT && this.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_LIST.length === 0){

        this.$Indicator.open({
          text: '加载中...',
          spinnerType: 'fading-circle'
        })

        this.axios.post('/api/cleanUnReadReply').then(data => {
          if (data.data.result === 'success') {
            this.$store.state.newsStatus = 0
          }
        }).catch(err => {
          console.log(err)
        })
        
        this.WORK_RANGE_ABOUT({
          page: this.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_PAGE
        }).then(data => {
          if (data.data.status == 0) {
            return
          }
          this.$Indicator.close()
        })
      }
    },
    destroyed () {
      window.onscroll = () => {
        return
      }
    },
    mounted () {
      document.documentElement.scrollTop = this.workRangeSavePosition
      window.onscroll = () => {
        if(this.scrollEvent()){
          this.dropDown()
        }
      }
    },
    methods: {
      changeAudioStatus(item, index){
        this.$store.commit('changeAudioStatus', {
          approvedAudio: item.approvedAudio, index
        })
        
        if(item.approvedAudioLength > 0){
          setTimeout(() => {
            item.playStatus = false
          }, item.approvedAudioLength * 1000)
        }
      },
      ...mapActions([
        'WORK_RANGE_ABOUT'
      ]),
      // 手势上滑加载数据
      dropDown() {
        if (this.pagination) {
          this.loading()
          this.pagination = false
          this.WORK_RANGE_ABOUT({
            page: ++this.WORK_RANGE_ABOUT_OBJECT.WORK_ABOUT_PAGE
          }).then(res => {
            this.hideLoading()
            if (res.data.result.list.length > 0) {
              this.pagination = true
            } else {
              this.$Toast({
                message: '没有更多了',
                position: 'middle',
                duration: 1000
              })
              this.pagination = false
            }
          })
        }
      },
      __enterPerpleCenter(item) {
        this.$router.push({
          name: 'perWorkCircle',
          params: {
            id: item.sendUserId
          }
        })
      },
      __enterTaskDetail(item) {
        this.$router.push({
          name: 'workRangeDetail',
          params: {
            taskId: item.taskId,
            jobcompletionId: item.jobcompletionid
          }
        })
      }
    }
  }
</script>



